.question {
  background: #eeeeee;
  margin: 1em 0;
  padding: 1em; }
  .question .header {
    font-style: italic; }
  .question .body {
    margin-bottom: 0.2em;
    margin-top: 0.2em; }
  .question .feedback {
    margin: 0.2em; }
  .question .question-icon, .question .question-icon-correct, .question .question-icon-incorrect {
    display: inline-block;
    font-size: 2.5em;
    margin-right: .1em;
    vertical-align: middle; }
    .question .question-icon-correct {
      color: #4bad38; }
    .question .question-icon-incorrect {
      color: #aa3232; }
