.matching-area {
  padding-top: .1em;
  display: flex; }
  .matching-area .answer-list {
    width: 40%;
    margin-right: 5%;
    margin-top: 0em; }
    .matching-area .answer-list .answer, .matching-area .answer-list .answer-correct, .matching-area .answer-list .answer-incorrect {
      padding: .8em; }
  .matching-area .matching-prompts {
    width: 40%; }
    .matching-area .matching-prompts-item {
      box-sizing: border-box;
      border-radius: 0.8em;
      border: solid 0.2em;
      border-color: rgba(255, 255, 255, 0);
      line-height: 1.8em;
      padding: 0.8em;
      margin: 0.25em;
      background: #e9e9e9; }
